import { all, call, fork } from 'redux-saga/effects';
import userSaga from './userSaga';
import englishSaga from './englishSaga';
import homeSaga from './homeSaga';
function* rootSaga() {
  yield all([
    yield fork(userSaga),
    yield fork(englishSaga),
    yield fork(homeSaga)
  ]);
}
export default rootSaga;