import { put, fork, take,takeLatest } from 'redux-saga/effects';
import Axios from 'axios';
import * as englishAction from '../action/englishAction';
import * as types from '../action/types';
import { englishCdn } from '../host';
const apiGetEnglishStory = `${englishCdn}/english/listStory.json`;
const apiGetEnglishListPhonic = `${englishCdn}/phonics/listPhonics_v3.json`;
// const apiGetEnglishBook = `${englishCdn}/english/`;

// put = vs dispatch
function* getEnglishStorySaga() {
  while (true) {
    yield take(types.GET_ENGLISH_STORY);
    try {
      const res = yield Axios.get(apiGetEnglishStory);
      const { data } = res;
      yield put(englishAction.getEnglishStorySuccess(data));
    } catch (err) {
      console.log(err);
      yield put(englishAction.getEnglishStoryErr(err));
    }
  }
}

function* getEnglishBookSaga({ data }) {
  const apiGetEnglishBook = `${englishCdn}/english/${data}/content.json`;
  console.log(apiGetEnglishBook);
  try {
    const res = yield Axios.get(apiGetEnglishBook);
    const { data } = res;
    console.log(data);
    yield put(englishAction.getEnglishBookSuccess(data));
  } catch (err) {
    console.log(err);
    yield put(englishAction.getEnglishBookErr());
  }
}

function* getEnglishListPhonicsSaga() {
  while (true) {
    yield take(types.GET_ENGLISH_LIST_PHONIC);
    try {
      const res = yield Axios.get(apiGetEnglishListPhonic);
      const { data } = res;
      yield put(englishAction.getEnglishListPhonicSuccess(data));
    } catch (err) {
      console.log(err);
      yield put(englishAction.getEnglishListPhonicErr(err));
    }
  }
}

function* getEnglishPhonicSaga({ data }) {
  const apiGetEnglishPhonic = `${englishCdn}/phonics/${data}.json`;
  try {
    const res = yield Axios.get(apiGetEnglishPhonic);
    const { data } = res;
    yield put(englishAction.getEnglishPhonicSuccess(data));
  } catch (err) {
    console.log(err);
    yield put(englishAction.getEnglishPhonicErr());
  }
}

function* englishStorySaga() {
  yield fork(getEnglishStorySaga);
  yield takeLatest(types.GET_ENGLISH_BOOK, getEnglishBookSaga);
  yield fork(getEnglishListPhonicsSaga);
  yield takeLatest(types.GET_ENGLISH_PHONIC, getEnglishPhonicSaga);
}
export default englishStorySaga;


//takeLatest:   chỉ cho phép một hàm xử lý có thể chạy tại một thời điểm 